  color: rgb(65, 65, 65);
  font-size: large;
  padding: 7px;
  line-height: 35px;
  margin-bottom: 25px;
 }
 .BoldText a:hover{
  text-decoration: none;
 }
 .BoldText{
  color: rgb(70, 70, 70);
  font-weight: 400;
